<template>
    <div class="cent">
        <form>
            <input type="text" placeholder="请输入电话号码"/>
            <input type="password" placeholder="请输入密码"/>
            <input class="btn" type="button" value="登录"/>
            <div class="inputA">
              <a href="#">注册账号</a>
              <a href="#">找回密码</a>
            </div>
        </form>
    </div>
</template>

<script>
 export default {
     name:"loginCent",
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang="scss" scoped>
    .cent{
        width:100%;
        form{
            width:100%;
            display: flex;
            flex-direction: column;
            input{
                display: block;
                height:.6rem;
                font-size: 14px;
                color:#91908f;
                
                border:none;
                width:100%;
                text-indent:.2rem;
                border-bottom:1px solid #e0e0e2;
                outline: 0;
            }
            .btn{
                margin:0 auto;
                width:1.5rem;
                height:.5rem;
                background:#e3008d;
                border:1px solid #e3008d;
                border-radius: .05rem;
                font-size:.16rem;
                color:white;
                text-align: center;
                margin-top:.3rem;
            }
            .inputA{
              display: flex;
              justify-content: space-between;
              margin-top:.3rem;
              a{
                font-size: .14rem;
                text-indent: .2rem;
                margin-right:.2rem;
                color:#8c8b89;

              }
            }
        }
    }
 
</style>



