<template>
    <div class="quanxuan">
        <input type="checkbox"/>
        <h3>全选</h3>
        <div class="quan">
            <p>
                <label>合计(不含运费)：</label>
                <span>￥</span>
                <span>0.00</span>
            </p>
            <p>
                <label>已优惠：</label>
                <b>￥</b>
                <b>0.00</b>
            </p>
        </div>
        <a href="javascript:;">去结算(<span>1</span>)</a>
    </div>
</template>

<script>
 export default {
   name:'carQuan',
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>
    .quanxuan{
        background:white;
        display: flex;
        height:.48rem;
        border-top:1px solid #eaeaea;
        border-bottom:1px solid #eaeaea;
        align-items: center;
        justify-content: space-between;
         position: fixed;
        bottom:.51rem;
        width:100%;
        input{
            margin-left:.15rem;
        }
        h3{
            font-size: .14rem;
            color:#222222;
            font-weight: 600;
        }
        .quan{
            display: flex;
            flex-direction: column;
            height:100%;
            p{
                display: flex;
                margin-top:.1rem;
                label{
                    font-size: 12px;
                    color:#222222;
                }
                span{
                    color:#ff5d4e;
                    font-size: 12px;
                    font-weight: 600;
                }
                b{
                    font-size: 12px;
                    color:#939393;
                }
            }
            p:last-child{
                margin-left:.44rem;
            }
            
        }
        a{
            display: block;
            width:1.08rem;
            height:.48rem;
            background:#ff6354;
            font-size: .16rem;
            color:white;
            text-align: center;
            line-height: .48rem;
        }
    }
 
</style>



