<template>

<li>
  <div class="shop-info">
      <input type="checkbox" @change="productCheckchange(sid,pid)" v-model="data.checked"  class="check goods-check goodsCheck">
      <div class="shop-info-img">
          <a href="#">
              <img :src="data.pic">
          </a>
      </div>
      <div class="shop-info-text">
          <h4>{{data.title}}</h4>
          <div class="shop-brief">
              <span>重量:{{data.attrs.weight}}</span>
              <span>颜色:{{data.attrs.color}}</span>
              <span>版本:{{data.attrs.ver}}</span>
          </div>
          <div class="shop-price">
              <div class="shop-pices">￥<b class="price">{{data.price}}</b>
              </div>
              <div class="shop-arithmetic">
                  <a class="minus" @click.prevent.default="minus(sid,pid)" >-</a>
                  <span class="num">{{data.qal}}</span>
                  <a class="plus" @click.prevent.default="add(sid,pid)">+</a>
              </div>
          </div>
      </div>
  </div>
</li>

</template>

<script>
 export default {
   name:'tbGoods',
   data () {
     return {

     }
   },
   components: {

   },
   props:["data","sid","pid","productCheckchange"],
   methods:{
     minus(sid,pid){
       this.$emit("minus",sid,pid)
     },
     add(sid,pid){
       this.$emit("add",sid,pid)
     }
   }
 }
</script>

<style lang='scss' scoped>

.shop-group-item ul li{border-bottom:1px solid #fff;}
.shop-group-item ul li:last-child{border-bottom:none;}
.shop-group-item{margin-bottom:5px;}
.shop-group-item ul li{border-bottom:1px solid #fff;}
.shop-group-item ul li:last-child{border-bottom:none;}
.shop-name{background:#fff;height:35px;line-height:35px;padding:0 15px;position:relative;}
.shop-name h4{float:left;font-size:14px;background:url(../../assets/icon-kin.png) no-repeat left center;background-size:20px 20px;padding-left:25px;margin-left: 28px;}
.shop-name .coupons{float:right;}
.shop-name .coupons span{display:inline-block;padding:0 5px;}
.shop-name .coupons em{color:#e0e0e0;}

.shop-info{
  background:#f5f5f5;
  height:120px;
  padding:0 15px;
  position:relative;
  // top:100px;
  }
.shop-info .checkbox{background:url(../../assets/icon_radio3.png) no-repeat left center;background-size:20px 20px;position:absolute;top:50%;left:15px;margin-top:-60px;width:20px;height:120px;}
.shop-info .checkbox1{background:url(../../assets/icon_radio4.png) no-repeat left center;background-size:20px 20px;position:absolute;top:50%;left:15px;margin-top:-60px;width:20px;height:120px;}
.shop-info .shop-info-img{position:absolute;top:15px;left:45px;width:90px;height:90px;}
.shop-info .shop-info-img img{width:100%;height:100%;}
.shop-info .shop-info-text{
  margin-left:130px;
  padding:15px 0;
  }
.shop-info .shop-info-text h4{font-size:14px;display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:2;overflow: hidden;width:215px;}
.shop-info .shop-info-text .shop-brief{height:25px;line-height:25px;font-size:12px;color:#81838e;white-space:nowrap;}
.shop-info .shop-info-text .shop-brief span{display:inline-block;margin-right:8px;}
.shop-info .shop-info-text .shop-price{height:24px;line-height:24px;position:relative;}
.shop-info .shop-info-text .shop-price .shop-pices {color:red;font-size:16px;}
.shop-info .shop-info-text .shop-arithmetic{position:absolute;right:0px;top:0px;width:84px;box-sizing:border-box;white-space:nowrap;height:100%;border:1px solid #e0e0e0;display: flex;justify-content: space-between}
.shop-info .shop-info-text .shop-arithmetic a{display:block;width:23px;height:22px;line-height:22px;text-align:center;background:#fff;font-size:16px;}
.shop-info .shop-info-text .shop-arithmetic .minus{border-right:1px solid #e0e0e0;}
.shop-info .shop-info-text .shop-arithmetic .failed{color:#d1d1d1;}
.shop-info .shop-info-text .shop-arithmetic .plus{border-left:1px solid #e0e0e0;}
.shop-info .shop-info-text .shop-arithmetic .num{ width:32px;text-align:center;border:none;display: inline-block;height:100%;box-sizing:border-box;vertical-align:top;margin:0 -6px;font-size: 16px;}
input[type="checkbox"]{
  -webkit-appearance:none;
  outline: none;
  }
input.check{background:url(../../assets/icon_radio3.png) no-repeat center left;background-size:20px 20px;position:absolute;top:50%;left:10px;margin-top:-18px;width:20px;height:35px;}
input.check:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
input.goodsCheck:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
input.check:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
.checked{background:url(../../assets/icon_radio4.png) no-repeat left center;background-size:20px 20px;position:absolute;top:50%;left:15px;margin-top:-18px;width:20px;height:35px;}

</style>



