<template>
    <div class="detail_num">
        <p>
            规格
            <span>12个/份</span>
            <span>8个/份</span>
        </p>
        <p>
            数量
            <a href="javascript:;">-</a>
            <input type="text" value="1"/>
            <a href="javascript:;">+</a>
        </p>
    </div>
</template>

<script>
 export default {
   name:'detailNum',
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>
.detail_num{
    background:white;
    margin-top:.1rem;
    p{
        display: flex;
        font-size: .14rem;
        color:#555555;
        align-items: center;
        padding-top:.1rem;
        padding-bottom:.1rem;
        padding-left:.11rem;
        padding-right:.11rem;
        border-bottom:1px solid #e9e9e9;
        span{
            display: block;
            width:.88rem;
            height:.29rem;
            border:2px solid #1ca476;
            font-size: 12px;
            color:#1ca476;
            border-radius: .05rem;
            text-align: center;
            line-height: .29rem;
            margin-left:.11rem;
        }
        a{
            display: block;
            font-size: .16rem;
            width:.25rem;
            height:.25rem;
            border:1px solid #e9e9e9;
            color:black;
            text-align: center;
            line-height: .25rem;

        }
        input{
            display: block;
            font-size: .16rem;
            color:black;
            border:none;
            border-top:1px solid #e9e9e9;
            border-bottom:1px solid #e9e9e9;
            text-align: center;
            line-height: .25rem;
            width:.38rem;
        }
        a:first-child{
            margin-left:.11rem;
        }
    }
}
 
</style>



