<template>
<div class="shop-group-item">
  <div class="shop-name">
      <input type="checkbox" @change="shopAllSelected(sid)" v-model="data.checked" class="check goods-check shopCheck">
      <h4><a href="#">{{data.title}}</a></h4>
      <div class="coupons">
          <span>领券</span><em>|</em><span>编辑</span>
      </div>
  </div>
  <tb-goods-list :productCheckchange="productCheckchange" @add="add" @minus="minus" :data="data.products" :sid="sid"></tb-goods-list>
  <div class="shopPrice">本店总计：￥<span class="shop-total-amount ShopTotal">{{data | countShopTotalPriceFilter}}</span>
  </div>
</div>

</template>

<script>
import tbGoodsList from './goodsList'
 export default {
   name:'tbShop',
   data () {
     return {

     }
   },
   components: {
     tbGoodsList
   },
   props:["data","sid","productCheckchange"],
   mounted(){
    //  console.log(this.data,this.sid);
   },
   methods:{
     minus(sid,pid){
       console.log(sid,pid)
       this.$emit("minus",sid,pid)
     },
     add(sid,pid){
       console.log(sid,pid)
       this.$emit("add",sid,pid)
     },
     shopAllSelected(sid){
        console.log(sid);
        this.$emit("shopChecked",sid);
     }

   },
   filters:{
            countShopTotalPriceFilter(data){
                let total=0
                data.products.forEach((goods,pid)=>{
                    if(goods.checked){
                        total +=goods.price * goods.qal;
                    }
                })

                return  (total).toFixed(2);
            }
        }
 }
</script>

<style lang='scss' scoped>

.shop-group-item{margin-bottom:5px;}
.shop-group-item ul li{border-bottom:1px solid #fff;}
.shop-group-item ul li:last-child{border-bottom:none;}
.shop-name{background:#fff;height:35px;line-height:35px;padding:0 15px;position:relative;}
.shop-name h4{float:left;font-size:14px;background:url(../../assets/icon-kin.png) no-repeat left center;background-size:20px 20px;padding-left:25px;margin-left: 28px;}
.shop-name .coupons{float:right;}
.shop-name .coupons span{display:inline-block;padding:0 5px;font-size: 16px;}
.shop-name .coupons em{color:#e0e0e0;font-size: 16px;}
.shopPrice{
  background:#fff;
  height:35px;
  line-height:35px;
  padding:0 15px;
  text-align:right;
  font-size: 16px;
  }
.shopPrice span{
  // display: block;
  color:#f00;
  font-size: 16px;
  }
  input[type="checkbox"]{
  -webkit-appearance:none;
  outline: none;
  }
input.check{background:url(../../assets/icon_radio3.png) no-repeat center left;background-size:20px 20px;position:absolute;top:50%;left:10px;margin-top:-18px;width:20px;height:35px;}
input.check:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
input.goodsCheck:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
input.check:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
.checked{background:url(../../assets/icon_radio4.png) no-repeat left center;background-size:20px 20px;position:absolute;top:50%;left:15px;margin-top:-18px;width:20px;height:35px;}

</style>



