 }
</script>

<style lang="scss" scoped>
    .seckill{
        margin-top:.12rem;
        .seckill_top{

            .seckill_one{
                display: flex;
                justify-content: space-between;
                align-items: center;
                height:.75rem;
                border-bottom:1px solid #f3f3f5;
                padding-left:.21rem;
                padding-right:.19rem;
                 background:white;
                h2{
                    font-size: .28rem;
                    color:black;
                    line-height: .28rem;
                }
                span{
                    display: block;
