<template>
    <div class="foot">
        <ul>
            <li @click="getId(index)" v-for="(item,index) in data" :key="item.id" class="liA">
                <router-link  class="indexA" :to="item.href" :index="index">
                    <img :src="item.img"/>
                    <span>{{item.spen}}</span>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import store from "./footer.js"
 export default {
   props:["data"],
     name:"homeFoot",
   data () {
     return {
        //
     }
   },
   components: {
        store
   },
   mounted(){

       this.$store.state.index=0
   },
   methods:{
       getId(i){
           this.$store.state.index=i
       }
   }
 }
</script>

<style lang="scss" scoped>
    .foot{
        background:white;
        height:.52rem;
        display:flex;
        align-items: center;
        border-top:1px solid #ecebf0;
        ul{
            width:100%;
            display:flex;
            justify-content: space-around;
            align-items: center;
            li{
                width:20%;
                text-align: center;
                a{
                    display: block;
                    img{
                        display: block;
                        width:.19rem;
                        height:.19rem;
                        margin:0 auto;
                    }
                    span{
                        display: block;
                        font-size:.14rem;
                        color:#181818;
                        line-height: .14rem;
                        margin-top:.11rem;
                    }
                }

            }
        }
    }

</style>



