<template>

   <ul>
     <tb-goods @add="add" @minus="minus" v-for="(item,index) in data" :productCheckchange="productCheckchange" :key="index" :pid="index" :sid="sid" :data="item"></tb-goods>
   </ul>
 <!-- v-for="(item,index) in data.products" :key="index" -->

</template>

<script>
import tbGoods from './goods'

 export default {
   name:'tbGoodsList',
   data () {
     return {

     }
   },
   components: {
     tbGoods
   },
   props:["data","sid","productCheckchange"],
   mounted(){
    //  console.log(this.data,this.sid);
   },
   methods:{
     minus(sid,pid){
       console.log(sid,pid)
       this.$emit("minus",sid,pid)
     },
     add(sid,pid){
       console.log(sid,pid)
       this.$emit("add",sid,pid)
     }
   }
 }
</script>

<style lang='scss' scoped>
.shop-group-item{margin-bottom:5px;}
.shop-group-item ul li{border-bottom:1px solid #fff;}
.shop-group-item ul li:last-child{border-bottom:none;}

</style>



