<template>
    <div class="header">
        <div class="searchheader">
            <a class="imgs" href="javascript:;" onclick="window.history.go(-1)">
                <img class="imgs" src="static/img/s1.png"/>
            </a>
            <a href="#">
                <img src="static/img/s2.png"/>
                <span>搜索分类商品</span>
            </a>
            <label>搜索</label>
        </div>
        <div class="header_two">
            <ul>
                <li v-for="item in headerList" :key="item.id">
                    <p :value="item.text">{{item.text}}</p>
                    <div class="imgp">
                        <img class="img1" src="static/img/s3.png"/>
                        <img class="img2" src="static/img/s4.png"/>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
 export default {
    name:"searchHeader",
   data () {
     return {
         headerList:[
             {
                 id:"01",
                 text:"综合"
             },
             {
                 id:"02",
                 text:"销量"
             },
             {
                 id:"03",
                 text:"价格"
             }
         ]
     }
   },
   components: {

   }
 }
</script>

<style lang="scss" scoped>
.header{
    background:white;
    .searchheader{
        height:.46rem;
        background:#e3008d;
        display: flex;
        justify-content: space-around;
        align-items: center;
        .imgs{
            display: block;
            width:.1rem;
            height:.16rem;
            background:transparent;
            img{
                
            }
        }
        a{
            display: flex;
            background:white;
            height:.3rem;
            width:2.49rem;
            border-radius: .04rem;
            align-items: center;
            img{
                display: block;
                width:.15rem;
                height:.15rem;
                margin-left:.08rem;
                margin-right:.09rem;
            }
            span{
                display: block;
                font-size: .14rem;
                color:#606060;
            }
        }
        label{
            display: block;
            font-size: .16rem;
            color:#fff;
        }
    }
    .header_two{
        border-bottom:1px solid #ecebf0;
        ul{
            display: flex;
            justify-content: space-around;
            align-items: center;
            height:.38rem;
            li{
                display: flex;
                
                p{
                    font-size: .16rem;
                    color:#7c7b82;
                }
                .imgp{
                    position: relative;
                    .img1{
                        position: absolute;
                        display: block;
                        width:.09rem;
                        height:.05rem;
                        top:10%;
                    }
                    .img2{
                        position: absolute;
                        display: block;
                        width:.09rem;
                        height:.05rem;
                        top:60%;
                    }
                }
            }
        }
    }
}
    
 
</style>



