<template>
    <div class="detailadd">
        <div class="add">
            <span>送至</span>
            <div class='add_one'>
                <div class="one">
                    <img src="static/img/c4.png"/>
                    <p>陕西 西安市 雁塔区</p>
                </div>
                <img src="static/img/c5.png" alt="">
            </div>
        </div>
        <div class="add_two">
            <span>有货</span>
            <p>
                16:00前完成订单，预计明日(3月4日)送达
            </p>
        </div>
    </div>
</template>

<script>
 export default {
   name:'detailAdd',
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>
    .detailadd{
        background:white;
        margin-top:.1rem;
        padding-top:.15rem;
        .add{
            display: flex;
            padding-left:.11rem;
            padding-bottom:.1rem;
            span{
                display: block;
                font-size: .14rem;
                color:#535353;
                margin-right:.12rem;
            }
            .add_one{
                display: flex;
                flex:1;
                justify-content: space-between;
                .one{
                    display: flex;
                    img{
                        display: block;
                        width:.12rem;
                        height:.15rem;
                        margin-right:.08rem;
                    }
                    p{
                        font-size: .13rem;
                        color:#222222;
                        font-weight: 600;
                    }
                }
                img{
                    display: block;
                    width:.07rem;
                    height:.13rem;
                    margin-right:.1rem;
                }
            }
        }
        .add_two{
            display: flex;
            margin-left:.45rem;
            padding-bottom:.17rem;
            span{
                display: block;
                font-size: 12px;
                color:#04ac71;
                margin-right:.06rem;
            }
            p{
                font-size: 12px;
                color:#7f7f7f;
            }
        }
    }
 
</style>



