<template>
    <div class="bigBox">
        <class-right :data="classList"></class-right>
    </div>
</template>

<script>
import classRight from './class/right'
import data from 'static/data.json'

 export default {
     name:"classnam",
   data () {
     return {
         classList:data.class
     }
   },
   components: {
       classRight
   }
 }
</script>

<style lang="scss" scoped>
        .bigBox{
          height:100%;
          .left{
              height:100%;
          }
        }
    
 
</style>



