<template>
    <div class="nav">
        <ul>
            <li v-for="(item,index) in data" :key="index">
                <img :src="item.navImg"/>
                <span>{{item.navText}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
 export default {
   props:["data"],
     name:"homeNav",
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang="scss" scoped>

 .nav{
     background:white;
     ul{
         display: flex;
        flex-wrap: wrap;
        padding-top:.07rem;
        padding-bottom:.1rem;
         li{
             flex-shrink: 0;
             justify-content: space-around;
             width:25%;
             text-align: center;
             img{
                 display: block;
                 width:.48rem;
                 height:.48rem;
                border-radius: 50%;
                margin:0 auto;
                margin-top:.12rem;
             }
             span{
                 display: block;
                 font-size: 12px;
                 color:#3e3e3e;
                 margin-top:.07rem;
             }
         }
     }
 }
</style>



