<template>
<div class="left">
    <ul class="uls">
        <li v-for="(item,index) in data" :key="index" @click="hh(index)">{{item.left}}</li>
    </ul>
    <div class="right">
        <!-- <ul v-show="num==index" v-for="(item,index) in data" :key="index">
            <li v-for="(u,index2) in item.right" :key="index2"  @click="getId(index2)">
                <a :href="item.href">{{u.text}}</a>
            </li>
        </ul> -->
        <ul v-show="num==index" v-for="(item,index) in data" :key="index">
            <li v-for="(u,index2) in data1.right" :key="index2"  @click="getId(index2)">
                <a :href="data1.href">{{u.text}}</a>
            </li>
        </ul>
    </div>
</div>
</template>
<script>

 export default {
     props:["data"],
     name:"classRight",
   data () {
     return {
        num:0,
        data1:0,
        data2:0
     }
   },
   components: {

   },
   mounted(){
        this.data1 = this.data[this.num];
   },
   methods:{
     hh(i){
            this.num = i;
            this.data1 = this.data[this.num]
        },
      getId(i){
        this.data2 = this.data1.right[i];
        let n = localStorage.getItem("num")
        localStorage.setItem("num",this.data2)
        console.log("data.right",this.data2);
      }
   }
 }
</script>

<style lang="scss" scoped>


    .left{
        display: flex;
        .uls{
            overflow-y: auto;
            width:.94rem;
            border-right:1px solid #c1c0c5;
            li{
                display: block;
                font-size: 12px;
                color:black;
                height:.51rem;
                line-height: .51rem;
                background:#f0eff4;
                border-bottom:1px solid #e4e4e5;
                text-align: center;
            }
            li:first-child{
                background:white;
            }
        }
    }
    .right{
        overflow-y: auto;
        flex:1;
        ul{
            background:white;
            display: flex;
            flex-wrap: wrap;
            li{
                flex-shrink: 0;
                width:40%;
                height:.51rem;
                border-bottom:1px solid #f4f4f4;
                padding-left:.22rem;
                justify-content: space-around;
                a{
                    display: block;
                    font-size: 12px;
                    color:black;
                    line-height: .51rem;
                }

            }
        }
    }

</style>



