<template>
    <div class="swiper-container">
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(i,index2) in data1" :key="index2">
                <img :src="i"/>
            </div>
        </div>
        <div class="swiper-pagination"></div>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import Swiper from 'swiper'
 export default {
   props:["data1"],
   name:'detailSwiper',
   data () {
     return {

     }
   },
   components: {

   },
   mounted(){
     console.log("轮播图数据",this.data1)
   }

 }
</script>

<style lang='scss' scoped>
@import 'swiper/dist/css/swiper.css';
 .swiper-container{
        .swiper-wrapper{
            .swiper-slide{
                img{
                    display: block;
                    width:3.75rem;
                    height:3.5rem;
                }
            }
        }
    }
 .swiper-pagination-bullet-active{
        background:#12b37a;
    }

    .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet{
        margin:0 2px;
    }
    .swiper-pagination-bullet{
        width: .055rem;
        height: .055rem;
    }
    .swiper-pagination-fraction[data-v-4ef25755], .swiper-pagination-custom[data-v-4ef25755], .swiper-container-horizontal > .swiper-pagination-bullets[data-v-4ef25755]{
        bottom: 1px;
    }
    .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet{
        margin:0;
    }
</style>



