<template>
    <div class="header">
        <h3>{{headerList[n]}}</h3>
    </div>

</template>

<script>
 import {mapGetters,mapActions} from "vuex"
 export default {
     name:"homeHeader",
     data(){
         return {
             headerList:["首页","分类","店中店","购物车","我的"]
         }
     },
     computed:{
            ...mapGetters({
                n:"GETNUM" //$emit("GETNUM")
            })
        },
        methods:{
            ...mapActions({
                update:"ADD"
            })
        }
 }




</script>

<style lang="scss" scoped>

    .header{
        height:.46rem;
        background:#e3008d;
        text-align: center;
        h3{
            font-size: .17rem;
            color:white;
            padding-top:.12rem
        }
    }
</style>



