import Vue from 'vue';
import Router from 'vue-router';
import home from '@/pages/home';
import classnam from '@/pages/class';
import search from '@/pages/search';
import register from '@/pages/register';
import login from '@/pages/login';
import VueRouter from "vue-router";
import Main from '@/main/Main';
import shop from '@/pages/shop';
import car from '@/pages/car';
import my from '@/pages/my';
import detail from '@/pages/detail';
import taoBao from '@/taobao/taoBao.vue';

Vue.use(Router)
Vue.use(VueRouter)

export default new Router({
  routes: [
    // {path:"/",redirect:"/pages"},
    {
      path:"/",
      component:Main,
      children:[
          {path:"",component:home},
          {path:"pages/home",component:home},
          {path:"pages/classnam",component:classnam},
          {path:"pages/shop",component:shop},
          {path:"pages/car",component:car},
          {path:"pages/my",component:my}
      ]
  },
    // {
    //   path: '/',
    //   name: 'home',
    //   component: home
    // },
    // {
    //   path: '/classnam',
    //   name: 'classnam',
    //   component: classnam
    // },
    {
      path: '/pages/search',
      name: 'search',
      component: search
    },
    {
      path: '/pages/register',
      name: 'register',
      component: register
    },
    {
      path: '/pages/login',
      name: 'login',
      component: login
    },
    {
      path: '/pages/detail',
      name: 'detail',
      component: detail
    },
    {
      path: '/taoBao',
      name: 'taoBao',
      component: taoBao
    }
  ]
})



