<template>
    <div class="wrap">
        <div class="swiper-container swiper-h">
            <div class="swiper-wrapper" v-for="(item,index) in data" :key="index">

                <div class="swiper-slide" v-for="(item,index) in item.home" :key="index">
                    <img :src="item.swiperImg">
                </div>
            </div>
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
// import  '../../../bower_components/swiper/dist/css/swiper.css'
// import Swiper from '../../../bower_components/swiper/dist/js/swiper.js'
 import Swiper from 'swiper'
import 'swiper/dist/css/swiper.css'
 export default {
     props:["data"],
     name:"homeSwiper",
     data(){
         return{

         }
     },
     mounted () {
        var mySwiper = new Swiper('.swiper-container',{
            autoplay: {
                disableOnInteraction: false,
            },
             pagination: {
                el: '.swiper-pagination',
            },
        });
    }
 }
</script>

<style lang="scss" scoped>

    .wrap{
        overflow:hidden;
        width:100%;
        height:2.08rem;
        .swiper-container{
            touch-action: none;
            width: 100%;
            height: 2.08rem;
            .swiper-slide{
                img{
                    display: block;
                    width: 100%;
                }
            }
            .swiper-pagination{
                bottom:-.02rem;
            }
        }
    }


</style>



