<template>
    <div class="bigBox">
        <detail-header></detail-header>
        <div class="center">
            <detail-swiper :data1="swiperList"></detail-swiper>
            <detail-one :data2="detailList"></detail-one>
            <detail-num></detail-num>
            <detail-add></detail-add>
            <detail-judge></detail-judge>
            <div class="four">
                <img src="static/img/c8.png"/>
                继续拖动查看图文详情
            </div>
            <detail-pic></detail-pic>
        </div>
        <detail-foot></detail-foot>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import Swiper from 'swiper'
import detailSwiper from './detail/swiper'
import detailOne from './detail/detail_one'
import detailNum from './detail/num'
import detailAdd from './detail/add'
import detailJudge from './detail/judge'
import detailFoot from './detail/foot'
import detailPic from './detail/pic'
import detailHeader from './detail/header'
import data from 'static/data'
import {mapGetters,mapActions} from "vuex"

 export default {
   name:'detail',
   data () {
     return {
        swiperList:data.class[0].right[0].banner,
        detailList:data.class[0].right[0].banner[0]
     }

   },
   components: {
       detailHeader,
       detailSwiper,
       detailOne,
       detailNum,
       detailAdd,
       detailJudge,
       detailFoot,
       detailPic
   },

    computed:{
        ...mapGetters({
            m:"GETNUM" //$emit("GETNUM")
        })
    },
    methods:{
        ...mapActions({
            update:"MDD"
        })

    },
    mounted () {
        var mySwiper = new Swiper('.swiper-container',{
            autoplay:false,
             pagination: {
                el: '.swiper-pagination',
            },
        });
        console.log("数据",this.detailList);
        console.log("this.m",this.m);
        let t = this.m;
        console.log("t",t)
        this.swiperList=this.swiperList[t].searchImg
    },

 }
</script>
<style lang='scss' scoped>
    .bigBox{
        height:100%;
        display: flex;
        flex-direction: column;
        .center{
            flex:1;
            overflow-y: auto;
            .four{
                display: flex;
                font-size: .14rem;
                color:#7c7c7c;
                margin-top:.3rem;
                padding-bottom:.2rem;
                align-items: center;
                img{
                    display: block;
                    width:.12rem;
                    height:.12rem;
                    margin-right:.06rem;
                    margin-left:1.07rem;
                }
            }
        }

    }
</style>



