<template>
    <div class="header">
        <img src="static/img/c9.png" onclick="window.history.go(-1)"/>
        <h3>商品详情</h3>
        <p>
            <img src="static/img/c10.png"/>
            <img src="static/img/c11.png"/>
        </p>
    </div>
</template>

<script>
 export default {
   name:'detailHeader',
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>
    .header{
        background:white;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top:.21rem;
        padding-bottom:.21rem;
        padding-left:.29rem;
        img{
            display: block;
            width:.18rem;
            height:.33rem;
        }
        h3{
            font-size: .2rem;
            color:#0f0f0f;
            margin-left: .5rem;
        }
        p{
            display: flex;
            align-items: center;
            margin-right:.25rem;
            img{
                display: block;
            }
            img:first-child{
                width:.3rem;
                height:.32rem;
                margin-right:.48rem;
            }
            img:last-child{
                width:.25rem;
                height:.05rem;
            }
        }

    }
 
</style>



