<template>
    <div class="shopcent">
        <p>
            <span>姓名:</span>
            <input type="text" placeholder="输入姓名"/>
        </p>
        <p>
            <span>性别:</span>
            <label class="dan">
                男<input type="radio" value="男" name="sex" checked/>
                女<input type="radio" value="女" name="sex"/>
            </label>
        </p>
        <p>
            <span>联系电话:</span>
            <input type="text" placeholder="请输入联系电话号码"/>
        </p>
        <p>
            <span>店中店名称:</span>
            <input type="text" placeholder="请输入店中店名称"/>
        </p>
        <div class="xuan">
            <input type="radio"/>店中店协议
        </div>
        <div class="btns">
            <input type="button" value="申请店中店"/>
        </div>
    </div>
</template>

<script>
 export default {
   name:'shopcent',
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>
 .shopcent{

     p{
         padding-left:.15rem;
         height:.5rem;
         display: flex;
         align-items: center;
         span{
             display: block;
             font-size: .14rem;
             color:#3a3a3a;
             width:.96rem;
             margin-right:.2rem;
         }
         input{
             font-size: .14rem;
             color:#595959;
             outline: 0;
         }
         .dan{
            font-size: .14rem;
             color:#3a3a3a;
             display: flex;
             input{
                 margin-left:.1rem;
                 margin-right:.44rem;
                 outline: 0;
             }
         }
     }
     .xuan{
         font-size: .14rem;
         color:#0a0a0a;
        margin-left:1.11rem;
        margin-top:.77rem;
        height:.16rem;
        display: flex;
        align-items: center;
         input{
             font-size: .16rem;
             color:#e3008d;
             margin-right:.49rem;
             outline: 0;
         }
     }
     .btns{
         margin-top:.28rem;
         margin-left:.15rem;
         height:.41rem;
         input{
             display: block;
             width:3.45rem;
             height:.41rem;
             background:#e3008d;
             font-size: .16rem;
             text-align: center;
             color:white;
             border-radius: .05rem;
             line-height: .41rem;
             outline: 0;
         }
     }
 }
</style>



