<template>
    <div class="Main">
        <home-header></home-header>
        <router-view></router-view>
        <home-foot :data="footerList"></home-foot>
    </div>
</template>

<script>
import homeHeader from '@/pages/home/header'
import homeFoot from '@/pages/footer/foot'
import data from 'static/data.json'
 export default {
    name:'Main',
   data () {
     return {
       footerList:data.footer
     }
   },
   components: {
     homeHeader,
     homeFoot
   }
 }
</script>

<style lang='scss' scoped>
  .Main{
    height:100%;
    display: flex;
    flex-direction: column;
    .bigBox{
      flex:1;
      overflow-y: auto;
    }
  }

</style>



