<template>
    <div class="cent">
        <form>
            <input class="i1" type="text" placeholder="请输入电话号码"/>
            <p class="error p1">手机号不正确</p>
            <p>
                <input @blur="bb()" class="i2" type="text" placeholder="请输入验证码"/>
                <span  @click="aa()" class="l2">获取验证码</span>
                <b class="p2">验证码不正确</b>
            </p>
            <input class="i3" type="text" placeholder="请输入用户名"/>
            <input type="text" placeholder="请输入昵称"/>
            <input class="i4" type="password" placeholder="请输入密码"/>
            <p class="error p4">6-20个字符，由字母、数字和符号的两种以上组合</p>
            <input class="i5" type="password" placeholder="请再次输入密码"/>
            <p class="error p5">密码与确认密码不一致</p>
            <div class="ra"><input class="inpra" type="radio"/>同意注册协议</div>
            <input class="btn" type="button" value="注册"/>
        </form>
    </div>
</template>

<script>
import './js/jquery-1.8.3.js'
// import './js/cent.js'

 export default {
     name:"registerCent",
   data () {
     return {
       num:0
     }
   },
   components: {

   },
   methods:{
       ff(){
        //    手机号码验证
           $(".i1").blur(
                function(){
                    var reg = /^1[3-9]\d{9}/i;
                    var str=$(this).val();
                    if(!reg.test(str)){
                        $(".p1").css({
                            "display":"block"
                        })
                    }else{
                        $(".p1").css({
                            "display":"none"
                        })
                    }

                }
            );
        // 密码验证
            $(".i4").blur(function(){
                var ret= /^[a-zA-Z0-9]{6,16}$/i;
                if(!ret.test($(this).val())){
                    $(".p4").css({
                        "display":"block"
                    })
                }else{
                    $(".p4").css({
                        "display":"none"
                    })
                }
            });
        // 重复密码验证
            $(".i5").blur(function(){
                if($(this).val()!=$(".i4").val()){
                    $(".p5").css({
                        "display":"block"
                    })
                }else{
                    $(".p5").css({
                        "display":"none"
                    })
                }
            });
       },
        aa(){
            let str="";
            for(let i=0;i<4;i++){
                let li = parseInt(Math.random()*16).toString(16);
                str+=li;
            }
            alert(str);

            let str1 = $(".l2").val();
            str1 =str;
            this.num = str;
            console.log(11111111111,str1);
        },
        // 验证码验证
        bb(){
          console.log(22222222,this.num);
          let str = document.querySelector(".i2").value;
          console.log(33333333,str)
          if(str!=this.num){
              $(".p2").css({
                  "display":"block"
              })
          }else{
              $(".p2").css({
                  "display":"none"
              })
          }
        }
   },
   mounted(){
       this.ff();
   }
 }
</script>
<style lang="scss" scoped>
    .cent{
        width:100%;
        form{
            width:100%;
            display: flex;
            flex-direction: column;
            position: relative;
            input{
                display: block;
                height:.6rem;
                font-size: 14px;
                color:#91908f;
                border:none;
                width:100%;
                text-indent:.2rem;
                border-bottom:1px solid #e0e0e2;
                outline: 0;
            }
            .error{
                display: none;
                font-size: .14rem;
                color:#fa2a2d;
                line-height: .1rem;
                margin-left:.2rem;
                z-index: 10;
                position: absolute;
            }
            .p1{
                top:.25rem;
                left:50%;
            }
            .p2{
                display: none;
                font-size: .14rem;
                color:#fa2a2d;
                line-height: .1rem;
                margin-left:.2rem;
                z-index: 10;
                position: absolute;
                top:.27rem;
                left:1rem;
            }
            .p4{
                top: 2.7rem;
                left:.3rem;
            }
            .p5{
                top: 3.3rem;
                left: 1.8rem;
            }
            p{
                position: relative;

                span{
                    position: absolute;
                    top:.1rem;
                    right:0.2rem;
                    display: block;
                    background:#e3008d;
                    width:1rem;
                    height:.4rem;
                    border-radius: .05rem;
                    font-size: 12px;
                    color:white;
                    line-height: .4rem;
                    text-align: center;
                }
            }
            .btn{
                margin:0 auto;
                width:1.5rem;
                height:.5rem;
                background:#e3008d;
                border:1px solid #e3008d;
                border-radius: .05rem;
                font-size:.16rem;
                color:white;
                text-align: center;
                margin-top:.2rem;
            }
            .ra{
                display: flex;
                font-size: .14rem;
                align-items: center;
                margin:0 auto;
                margin-top:1rem;
                .inpra{
                    display:flex;
                    width:.2rem;
                    height:.2rem;
                    margin-right:.05rem;
                }
             }
        }

    }

</style>



