<template>
 <div>
   <tb-header></tb-header>
   <tb-shop-list @add="addQel" @minus="minusQel" :productCheckchange="productCheckchange" @shopChecked="shopCheckedAll" :data="data1.shops"></tb-shop-list>
   <tb-zongjia @cartsChecked="cartsCheckedAllChecked" :data="data1"></tb-zongjia>
 </div>
</template>

<script>
// import cartsApi from './apis-serve/cartsApi.js'
import tbHeader from './page/header'
import tbShopList from './page/shopList'
import tbZongjia from './page/zongjia'
import cartsInfo from './apis/cartsInfo-data.json'

 export default {
   name:'taoBao',
   data() {
     return {
       data1:cartsInfo
     }
   },
   components: {
     "tb-shop-list":tbShopList,
     "tb-zongjia":tbZongjia,
     "tb-header":tbHeader
   },
   created(){
        this. _counteTotalMoney()
      },
   methods:{
     _counteTotalMoney(){
      let totalPrice = 10;
        this.data1.shops.forEach((shop,sid)=>{
            shop.products.forEach((goods,pid)=>{
                if(goods.checked){
                    totalPrice += goods.price * goods.qal;
                }
            })
        })
        this.data1.totalPrice = (totalPrice).toFixed(2);
    },
     minusQel(sid,pid){
       console.log(sid,pid)
       if(this.data1.shops[sid].products[pid].qal<=1){
         this.data1.shops[sid].products[pid].qal=1;
       }else{
         this.data1.shops[sid].products[pid].qal--;
       }
        this._counteTotalMoney()
     },
     addQel(sid,pid){
       console.log(sid,pid)
       this.data1.shops[sid].products[pid].qal++;
       this._counteTotalMoney()
     },
     shopCheckedAll(sid){
       console.log(sid)
       let checked = this.data1.shops[sid].checked;
        this.data1.shops[sid].products.forEach((goods,pid)=>{
            goods.checked= checked;
        });

        let isAllChecked=true;

        this.data1.shops.forEach((shop,sid)=>{
            if(!shop.checked){
              isAllChecked=false;
            }
        });
        this.data1.checked=isAllChecked;
     },
     cartsCheckedAllChecked(sid,pid){
       let checked = this.data1.checked;
       this.data1.shops.forEach((shop,sid)=>{
          shop.checked=checked;
          shop.products.forEach((goods,pid)=>{
            goods.checked = checked;
          })
       })
     },
     productCheckchange(sid,pid){
          let isChecked=true;
         //循环店铺的商品
          this.data1.shops[sid].products.forEach((goods,pid)=>{
            if(!goods.checked){
              isChecked=false;
            }
          })
          this.data1.shops[sid].checked=isChecked;
          //根据商品的状态改变全选反选的状态

          let isAllChecked=true;

          this.data1.shops.forEach((shop,sid)=>{
            shop.products.forEach((goods,pid)=>{
              if(!goods.checked){
                isAllChecked=false;
              }
            })
          })
          this.data1.checked=isAllChecked;
        },
   },



 }
</script>

<style lang='scss' scoped>


</style>



