<template>
<div class="payment-bar">
        <div class="all-checkbox">
            <input type="checkbox" @change="cartsChecked" v-model="data.checked"   class="check goods-check">全选
        </div>
        <div class="shop-total">
            <strong>总价：<i id="AllTotal" class="total">{{data.totalPrice}}</i></strong>
            <span>减免：100</span>
        </div>
        <a href="#" class="settlement">结算</a>
    </div>
</template>

<script>
 export default {
   name:'tbZongjia',
   data () {
     return {

     }
   },
   components: {

   },
   props:["data"],
   methods:{
     cartsChecked(){
       this.$emit("cartsChecked");
     }
   }

 }
</script>

<style lang='scss' scoped>

.payment-bar{
  clear:both;
  overflow:hidden;
  width:100%;
  height:49px;
  position:fixed;
  bottom:0;
  border-top:1px solid #e0e0e0;
  background:#fff;
  .all-checkbox{
    float:left;
    line-height:49px;
    padding-left:40px;
    font-size: 16px;
    }
    .shop-total{
      float:left;
      -webkit-box-flex:1.0;
      box-flex:1.0;
      margin:9px 20px 9px 35px;
      strong{
        display:block;
        font-size:16px;
        }
      span{
        display:block;
        font-size:12px;
        }
      }
      .settlement{
      display:inline-block;
      float:right;
      width:70px;
      height:49px;
      line-height:49px;
      text-align:center;
      color:#fff;
      font-size:16px;
      background:#f23030;
      }
  }
input[type="checkbox"]{
  -webkit-appearance:none;
  outline: none;
  }
input.check{background:url(../../assets/icon_radio3.png) no-repeat center left;background-size:20px 20px;position:absolute;top:50%;left:10px;margin-top:-18px;width:20px;height:35px;}
input.check:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
input.goodsCheck:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
input.check:checked{background:url(../../assets/icon_radio4.png) no-repeat center left;background-size:20px 20px;}
.checked{background:url(../../assets/icon_radio4.png) no-repeat left center;background-size:20px 20px;position:absolute;top:50%;left:15px;margin-top:-18px;width:20px;height:35px;}


</style>



