<template>
    <div class="mycentent">
        <div class="mycent">
            <img src="static/img/m9.png" alt="">
        </div>
        <p> </p>
        <div class="cent">
            <ul>
                <li v-for="item in mycentList" :key="item.id">
                    <img :src="item.img" alt="">
                    <span :value="item.span">{{item.span}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
 export default {
   name:'myCent',
   data () {
     return {
         mycentList:[
             {
                 id:"01",
                 img:"static/img/m1.png",
                 span:"我的订单"
             },
             {
                 id:"02",
                 img:"static/img/m2.png",
                 span:"个人信息"
             },
             {
                 id:"03",
                 img:"static/img/m3.png",
                 span:"商家管理"
             },
             {
                 id:"04",
                 img:"static/img/m4.png",
                 span:"成为商家"
             },
             {
                 id:"05",
                 img:"static/img/m5.png",
                 span:"余额管理"
             },
             {
                 id:"06",
                 img:"static/img/m6.png",
                 span:"消息盒子"
             },
             {
                 id:"07",
                 img:"static/img/m7.png",
                 span:"我的优惠券"
             },
             {
                 id:"08",
                 img:"static/img/m8.png",
                 span:"关于我们"
             }

         ]
     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>
    .mycentent{
        height:100%;
        display: flex;
        flex-direction: column;
        background:white;
        .mycent{
            padding-top:.12rem;
            height:.9rem;
            background:white;
            img{
                display: block;
                width:.78rem;
                height:.78rem;
                border-radius: .1rem;
                margin-left:.23rem;
                
            }
        }
        p{
            width:100%;
            height:.1rem;
            background:#f0eff4;
        }
        .cent{
            background:white;
            flex:1;
            ul{
                display: flex;
                flex-wrap: wrap;
                li{
                    flex-shrink: 0;
                    width:1.24rem;
                    border-right:1px solid #e9e9e9;
                    border-bottom:1px solid #e9e9e9;
                    text-align: center;
                    padding-bottom:.16rem;
                    img{
                        display: block;
                        width:.32rem;
                        height:.27rem;
                        margin:0 auto;
                        margin-top:.11rem;
                    }
                    span{
                        display: block;
                        font-size: 12px;
                        color:#070707;
                        margin-top:.15rem;
                    }
                }
                li:nth-child(3n){
                    border-right:none;
                }
            }
        }
    }
 
</style>



