<template>
    <div class="bigBox">
        <search-header></search-header>
        <div class="centent">
            <search-banner :data="bannerList"></search-banner>

        </div>
    </div>
</template>
<script>
import searchHeader from './search/header'
import searchBanner from './search/banner'
import data from 'static/data.json'

 export default {
     name:"search",
   data () {
     return {
         bannerList:data.class,
         data1:0
     }
   },
   components: {
       searchHeader,
       searchBanner
   },
   created(){
           this.initData()
        },
   methods:{
     initData(){
        let n = localStorage.getItem("num")
       this.data1 = n;
    }
   },
   mounted(){
     console.log(this.data1);
  },

 }
</script>

<style lang="scss" scoped>
    .bigBox{
        height:100%;
        display: flex;
        flex-direction: column;
        .centent{
            flex: 1;
            overflow-y: auto;
            background:white;
        }
    }

</style>



