<template>
    <div class="kill">
      <ul>
        <li v-for="(item,index) in data" :key="index">
          <a href="#">
            <img :src="item.compImg"/>
            <h3>{{item.compTitle}}</h3>
            <p>{{item.compAttribute}}</p>
            <div class="one">
              <label>奖励{{item.compPrice}}元</label>
              <span>已有{{item.compSingular}}购买</span>
            </div>
            <div class="two">
              <span>￥{{item.compTotalPrice}}</span>
              <b>&gt;{{item.compDay}}天后结束</b>
            </div>
          </a>
        </li>
      </ul>
    </div>
</template>

<script>
 export default {
   props:["data"],
     name:"secKill",
   data () {
     return {

     }
   },
   components: {

   }
 }
</script>

<style lang="scss" scoped>
    .kill{
        background:white;
        ul{
          display: flex;
          flex-wrap: wrap;
          padding-top:.1rem;
          li{
            flex-shrink: 0;
            margin-left:.15rem;
            padding-bottom:.13rem;
            a{
              display: block;
              img{
                display: block;
                width:1.67rem;
                height:1.69rem;
              }
              h3{
                font-size: .14rem;
                color:#0c0c0c;
                margin-top:.08rem;
              }
              p{
                font-size: 12px;
                color:#6d6d74;
                margin-top:.12rem;
              }
              .one{
                display: flex;
                font-size: 12px;
                color:#0c0c0c;
                margin-top:.12rem;
                width:1.67rem;
                justify-content: space-between;
              }
              .two{
                display: flex;
                margin-top:.13rem;
                width:1.67rem;
                justify-content: space-between;
                span{
                  font-size: 14px;
                  color:#050505;
                }
                b{
                  font-size: 12px;
                  color:#ff2525;
                }
              }
            }
          }
        }
    }

</style>



