import Vue from "vue"
import Vuex from "vuex"

 Vue.use(Vuex)
 let store =new Vuex.Store({
    // 共享的状态
    state:{
        index:0
    },
    actions:{
        ADD(store,paylods){
            store.commit("INC",payloads)
        }
    },
    mutations:{
        INC(state,args) {
            state.index;
        }
    },
    getters:{
        "GETNUM":(state)=>{
            return state.index;
        }
    }
})
export default store;



