<template>
    <div class="banner">
      <div class="bh">
        <h3>没有更多数据了</h3>
      </div>
      <div class="banner_one">
        <ul v-for="(item,index) in data" :key="index">
          <li v-for="(i,index1) in item.right[n].banner" :key="index1">
            <a :href="i.searchPath">
              <img :src="i.searchImg[0]"/>
              <div class="one_top">
                <h3>{{i.searchTitle}}</h3>
                <label>{{i.searchCharacteristics}}</label>
                <p>
                  <b>￥{{i.searchPrice}}</b>
                  <span class="span1">{{i.searchSingle}}</span>
                  <span class="span2">+</span>
                </p>
              </div>
            </a>
          </li>
        </ul>
         <!-- <ul v-for="(item,index) in data" :key="index">
          <li v-for="(i,index1) in item.banner" :key="index1">
            <a :href="i.searchPath">
              <img :src="i.searchImg[0]"/>
              <div class="one_top">
                <h3>{{i.searchTitle}}</h3>
                <label>{{i.searchCharacteristics}}</label>
                <p>
                  <b>￥{{i.searchPrice}}</b>
                  <span class="span1">{{i.searchSingle}}</span>
                  <span class="span2">+</span>
                </p>
              </div>
            </a>
          </li>
        </ul> -->
      </div>
    </div>
</template>

<script>

 export default {
   props:["data"],
     name:"searchBanner",
   data () {
     return {
       n:0
     }
   },
   components: {

   },
    mounted(){
    },
    methods:{

    }
 }
</script>

<style lang="scss" scoped>
  .banner{
    .bh{
      padding-top:.2rem;
      text-align: center;
      display: none;
      h3{
        font-size: .16rem;
        color:#777777;
        font-weight: 800;
      }
    }
    .banner_one{
      ul{
        li{

          height:1.35rem;
          border-bottom:1px solid #eeeeee;
          a{
            display: flex;
            img{
              display: block;
              width:.85rem;
              height:.80rem;
              margin-left:.3rem;
              margin-top:.27rem;
            }
            .one_top{
              flex: 1;
              margin-top:.16rem;
              margin-left:.25rem;
              h3{
                font-size: .14rem;
                color:#111111;
                font-weight: 600;
              }
              label{
                display: block;
                font-size: 14px;
                color:#a8a8a8;
                margin-top:.11rem;
                width:2.35rem;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
              }
              p{
                display: flex;
                margin-top:.54rem;
                position: relative;
                width:100%;
                b{
                  display: block;
                  font-size: 12px;
                  color:#ff6455;
                }
                .span1{
                  display: block;
                  font-size: 12px;
                  color:#8e8e8e;
                  margin-left:.1rem;
                }
                .span2{
                  display: block;
                  width:.25rem;
                  height:.25rem;
                  border:1px solid #00a867;
                  border-radius: 50%;
                  font-size: 14px;
                  color:#00a867;
                  line-height: .25rem;
                  text-align: center;
                  position: absolute;
                  bottom:0rem;
                  right:.23rem;
                }
              }
            }
          }

        }
      }
    }
  }

</style>



