<template>

   <div class="shopping">
     <tb-shop @add="add" @minus="minus" :productCheckchange="productCheckchange" @shopChecked="shopChecked" v-for="(item,index) in data" :key="index" :sid="index" :data="item"></tb-shop>
    </div>


</template>

<script>
import tbShop from './shop'
 export default {
   name:'tbShopList',
   data () {
     return {

     }
   },
   components: {
     tbShop
   },
   props:["data","productCheckchange"],
   mounted(){
    //  console.log(this.data);
   },
   methods:{
     shopChecked(sid){
        console.log(sid)
        this.$emit("shopChecked",sid)
     },
     minus(sid,pid){
       console.log(sid,pid)
       this.$emit("minus",sid,pid)
     },
     add(sid,pid){
       console.log(sid,pid)
       this.$emit("add",sid,pid)
     }
   }
 }
</script>

<style lang='scss' scoped>

.shopping{clear:both;overflow:hidden;height:auto;padding-bottom: 60px;}

</style>



