<template>
        <div class="you">
            <label>-- 猜你喜欢 --</label>
            <ul>
                <li v-for="item in youList" :key="item.id">
                    <img :src="item.img"/>
                    <div class="he">
                        <h3>{{item.h3}}</h3>
                        <p>
                            <span>￥{{item.span}}</span>
                            <a href="javascript:;">+</a>
                        </p>
                    </div>
                </li>
            </ul>
        </div>
</template>

<script>
 export default {
   name:'carGuess',
   data () {
     return {
         youList:[
             {
                 id:"01",
                 img:"static/img/m10.png",
                 h3:"鲜美来虾滑200g",
                 span:"39.90"
             },
             {
                 id:"02",
                 img:"static/img/m11.png",
                 h3:"山东栖霞优质红富士12个约200g/个",
                 span:"88.80"
             },
             {
                 id:"03",
                 img:"static/img/m12.png",
                 h3:"泰国龙眼 1kg",
                 span:"32.80"
             }
         ]
     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>
    
        .you{
            background:white;
            margin-top:.1rem;
            label{
                display: block;
                height:.5rem;
                text-align: center;
                font-size: .14rem;
                color:black;
                line-height: .5rem;
                font-weight: 600;
            }
            ul{
                display: flex;
                flex-wrap: wrap;
                margin-left:.3rem;
                margin-right:.25rem;
                li{
                    flex-shrink: 0;
                    width:50%;
                    border:none;
                    height:1.96rem;
                    padding-top:.25rem;
                    padding-bottom:.25rem;
                    img{
                        display: block;
                        width:1.03rem;
                        height:1.07rem;
                    }
                    .he{
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        h3{
                            font-size: .14rem;
                            color:#1c1c1c;
                            font-weight: 600;
                            margin-top:.28rem;
                            width:1.5rem;
                            white-space:nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                        p{
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            margin-right:.15rem;
                            margin-top:.33rem;
                            span{
                                font-size: 12px;
                                font-weight: 600;
                                color:#ff6455;
                            }
                            a{
                                display: block;
                                width:.25rem;
                                height:.25rem;
                                border-radius: 50%;
                                border:1px solid #00a666;
                                font-size: .18rem;
                                color:#00a666;
                                text-align: center;
                                line-height: .25rem;
                            }
                        }
                    }
                    
                }
            }
        }

 
</style>



