<template>
    <div class="carcent">
            <ul>
                <li v-for="item in carList" :key="item.id">
                    <input type="checkbox"/>
                    <img :src="item.img"/>
                    <div class="cent">
                        <h3 :value="item.h3">{{item.h3}}</h3>
                        <p>
                            <span :value="item.span">￥{{item.span}}</span>
                            <label>
                                <a href="javascript:;">-</a>
                                <input type="text" value="1"/>
                                <a href="javascript:;">+</a>
                            </label>
                        </p>
                    </div>
                </li>
            </ul>
    </div>
</template>

<script>
 export default {
   name:'',
   data () {
     return {
         carList:[
             {
                 id:"01",
                 img:"static/img/m10.png",
                 h3:"鲜美来虾滑200g",
                 span:"39.90"
             },
             {
                 id:"02",
                 img:"static/img/m11.png",
                 h3:"山东栖霞优质红富士12个约200g/个",
                 span:"88.80"
             }
         ]

     }
   },
   components: {

   }
 }
</script>

<style lang='scss' scoped>

    .carcent{
        background:white;

            ul{
                li{
                    display: flex;
                    height:1.15rem;
                    align-items: center;
                    border-bottom:1px solid #ebebeb;
                    padding-right:.17rem;
                    input{
                        display: block;
                        // width:.2rem;
                        // height:.2rem;
                        margin-left:.11rem;
                    }
                    img{
                        display: block;
                        width:.68rem;
                        height:.58rem;
                        margin-left:.23rem;
                    }
                    .cent{
                        margin-left:.23rem;
                        height:100%;
                        display: flex;
                        flex:1;
                        flex-direction: column;
                        justify-content: space-between;
                        h3{
                            font-size: .14rem;
                            color:#1c1c1c;
                            font-weight: 600;
                            margin-top:.15rem;
                        }
                        p{
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            padding-bottom:.1rem;
                            width:100%;
                            span{
                                display: block;
                                font-size: 12px;
                                color:#fd5848;
                                font-weight: 600;
                            }
                            label{
                                display: flex;

                                a{
                                    display: block;
                                    font-size: 12px;
                                    color:#c1c1c1;
                                    width:.25rem;
                                    height:.25rem;
                                    border:1px solid #dedede;
                                    line-height: .25rem;
                                    text-align: center;
                                }
                                input{
                                    display: block;
                                    font-size: 12px;
                                    color:#252525;
                                    border:1px solid #dedede;
                                    width:.37rem;
                                    height:.25rem;
                                    text-align: center;
                                    margin:0;
                                }
                                a:first-child{
                                    border-top-left-radius: .05rem;
                                    border-bottom-left-radius: .05rem;
                                }
                                a:last-child{
                                    border-top-right-radius: .05rem;
                                    border-bottom-right-radius: .05rem;
                                }
                            }
                        }
                    }
                }
            }
    }

</style>



